#signupImagebg {
    background-color: white;
    background-image: url('../../Images/loginPage2.jpg');
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
    margin-bottom: 2em;
}

#details label {
    color: black;
    margin-top: 0.7em;
}

#signupDetailsbg {
    width: 25vw;
    margin: 0 auto;
    padding-left: 4rem;
    padding-right: 3rem;
    padding-top: 1rem;
    /* background-color:  #fcd365; */
    text-align: start;
}

#details {
    display: flex;
    flex-direction: column;
    align-items: center;
}

#signupImage{
    height: 17vh;
    width: 17vh;
    background-image: url('../../Images/loginCircle1.jpg');
    background-position: center;
    background-size: cover;
    background-repeat: no-repeat;
    border-radius: 50%;
    display: flex;
}

#signupHeading {
    color: black;
    font-size: 1.5em;
}

.signupInput {
    height: 2em;
    font-family: inherit;
    font-size: 1em;
    appearance: none;
    margin-top: 0.3em;
    border-style: solid;
    border-color: gainsboro;
    border-width: 1px;
    border-radius: 3px;
    padding: 0rem .5rem;
    background-repeat: no-repeat;
    background-attachment: scroll;
    background-size: 16px 18px; 
    background-position: 98% 50%; 
    cursor: auto;
}

.w-full{
    width: 100%;
}

.buttonStyle {
    display: block;
    color: black;
    text-decoration: none;
    text-align: center;
    background-color: #fdc732;
    margin: 1em auto;
    border-style: solid;
    border-color: gainsboro;
    border-width: 1px;
    border-radius: 5px;
    padding: .5rem 5vw;
    transition: all 0.3s;
    font-family: 'Source Serif Pro', sans-serif;
    font-size: 1em;
    cursor: pointer;
    width: 100%;
}

#signupButton{
    margin-top: 1em;
    display: flex;
    justify-content: space-between;
    width: 100%;
    font-size: 0.8rem;
}

.gray{
    color: rgb(172, 171, 171) !important;
}

.algCenter{
    display: flex;
    justify-content: center;
    align-items: center;
}

.buttonStyle:hover{
    background-color: #eeb006;
}

.margin1{
    margin: 2em 0 1em 0;
}

.margin2{
    margin: 1em 0 1em 0;
}

.link{
    color: #6301ff;
}

.link:visited{
    color: #6301ff;
}

.link:hover, .link:active{
    text-decoration: underline;
}

input {
    overflow: visible;
}


@media (max-width: 850px) {
    .container1{
        grid-template-columns: 1fr;
        grid-template-rows: 1fr;
        background-image: url('../../Images/loginPage3.jpg');
        
        background-size: cover;
        background-position: center;
    }

    #signupDetailsbg{
        width: 50vw;
        margin-top: 1em;
        padding-top: 2em;
        padding-bottom: 1em;
        background-color: white;
        box-shadow: rgb(0, 11, 49) 0em 2em 1em 5px;
    }

}

@media (max-width: 500px) {
    .container1{
        grid-template-columns: 1fr;
        grid-template-rows: 1fr;
    }

    #signupDetailsbg{
        margin: 0em;
        height: 100%;
        width: 100%;
    }

}