.container3 {
	min-width: 500px;
	min-height: 280px;
	-webkit-transition: 1.5s ease-in-out;
	transition: 1s ease-in-out;
	transform-style: preserve-3d;
    margin: 2em 1.5em;
	border-radius: 10px;
    background-color: white;
	box-shadow: 0px 7px 14px 3px rgb(34 35 58 / 50%);
}

.side {
	position: absolute;
	text-align: center;
	width: 100%;
	height: 100%;
	padding: 0;
	color: #000;
	transform-style: preserve-3d;
	backface-visibility: hidden;
}

.profileImage {
    border-radius: 50%;
    height: 7em;
    width: 7em;
    object-fit: cover;
	border: 6px solid white;
	position: relative;
	top: 20%;
	left: 40%;
}

.content {
	height: 100%;
	transform: translatez(70px) scale(0.8);
	line-height: 1.5em;
    filter: blur(0) !important;
	display: grid;
	grid-template-columns: 3fr 7fr;
}

.content div{
	display: flex;
	flex-direction: column;
	align-content: center;
	justify-content: space-evenly;
	margin-bottom: 1em;
}

.content p {
	margin-top: 20%;
	line-height: 2em;
}

.cardImageFront{
    position: absolute;
    width: 30%;
    height: 100%;
	background-size: cover;
    filter: blur(2px);
    filter: brightness(0.5);
	border-radius: 10px 0 0 10px;
    z-index: 2;
}

.front {
	z-index: 2;
	border-radius: 10px;
}

.cardImageBack{
    z-index: 0;
    position: absolute;
    width: 100%;
    height: 100%;
	background-size: cover;
    filter: blur(2px);
    filter: brightness(0.5);
	border-radius: 10px;
    transform: rotateY(180deg);
}

.back {
	border-radius: 10px;
	transform: rotateY(180deg);
	z-index: 0;
    align-items: center;
    justify-content: center;
}

.container3:hover {
	-webkit-transform: rotateY(180deg);
	transform: rotateY(180deg);
}
.backContent{
	display: flex;
	flex-direction: column;
	justify-content: space-around;
	height: 90%;
	align-items: center;
}
.backContent button{
	height: 20%;
	cursor: pointer;
	width: 60%;
}

.glassVideo {
	display:inline-block;
	position: relative;
	background: transparent;
	border: 2px solid #0074d9;
	color: #0074d9;
	padding: 1em 2em;
	text-transform: uppercase;
	text-decoration: none;
	border-radius: .5em;
	-webkit-transition: .3s ease;
	transition: .3s ease;
	z-index: 1;
  
}
	.glassVideo:hover{
	  color: #fff;
	}
  
	.glassVideo:hover:before {
	  right: -1px;
	}
  
  .glassVideo::before {
	content: "";
	display: block;
	position: absolute;
	background: #0074d9;
	transition: right .3s ease;
	z-index: -1;
	border-radius: .5em;
	top: -1px;
	bottom: -1px;
	left: -1px;
	right: 105%;
	-webkit-backface-visibility: hidden;
  }
