.main{
    position: absolute;
    width: 100vw;
    height: 100vh;
    min-height: 90vh;
    margin-top: 1vh;
    left: 0;
    background-image: linear-gradient(to top, #96fbc4 0%, #97A74C 100%);
    overflow-x: hidden;
}
.container2{
    display: flex;
    width: 100%;
    min-height: 100vh;
    flex-wrap: wrap;
    justify-content: space-around;
}

.item{
    width: 25%;
    height: 40vh;
    margin-top: 2vh;
    color: black;
    display:flex;
    flex-direction: column;
    align-items: center;
    flex-wrap: wrap;
}

.item:nth-child(even){
    background: #cff7d9;
}
.item:nth-child(odd){
    background: #e6faeb;
    outline: 1px solid #EFF7F1;
}
.item img{
    width: 25%;
    height:30%;
    border-radius: 50%;
}
