body{
  overflow-x: hidden;
}

.App {
  color: white;
  
  font-family: 'Trebuchet MS', sans-serif;
  font-weight: normal;
}

/* .App-logo {
  height: 40vmin;
  pointer-events: none;
} */

.link{
  color: white;
  text-decoration: none;
}

.menuDivActive{
  background-color: rgb(243, 237, 236);
  color: white;
  animation: menuAnimation 1s;
  height: 14vh;
  position: absolute;
  width: 100vw;
  top: 0;
  left: 0;
  display: flex;
}
.links{
  display: flex;
  justify-content: space-evenly;
  width: 40vw;
  margin: auto;
  margin-bottom: 2vmin;
  margin-right: 35vw;
  background: transparent;
}

@keyframes menuAnimation {
  from {
    height: 0vh;
  }
  to {
    height: 14vh;
  }
}

.menuButton{
  font-size: 24px;
  cursor: pointer;
  border: none;
  background-color: transparent;
  color:black;
  margin-top: 3%;
  font-family: "BrandonGrotesqueWeb-Light";
  font-weight: normal;

}
.closeButton{
  font-size: 24px;
  cursor: pointer;
  border: none;
  background-color: transparent;
  color:black;
  margin-top: 5%;
  font-family: "BrandonGrotesqueWeb-Light";
  font-weight: normal;

}
.nav{    
  display: flex;
  margin: auto;
  border-bottom: 1px solid rgb(0, 0, 0);
  justify-content: space-between;
  align-items: center;
  width: 85vw;

}
.appoint{  
  display: inline-flex;
  justify-content:center;
  align-items:center;
  color: black;
  text-decoration: none;
  height: 100%;
  width: 48%;
  background-color: #AE5339;
  /* box-shadow: inset 0 0 2000px rgba(255, 255, 255, .5); */
}
.appointMain{
  margin-top: 2vh;
  height: 80vh;
  width: 99vw;
  display: flex;
  justify-content: space-around;
}
svg{
  width: 20px;
  height:20px
}
.experts{
  text-decoration: none;
  color: rgb(255, 255, 255);
  font-size: 16px;
  font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
  margin: 3vw;
  font-weight: 600;
}
.experts:hover{
  color: rgb(192, 190, 190);
}
.home{
  text-decoration: none;
  font-size: 24px;
  color: rgb(255, 255, 255);
  font-weight: 600;
  margin-right: 20vw;
}
.glassbutton{
  position: relative;
  display: inline-block;
  /* padding: 15px 25px; */
  background-color: #0c0c0c;
  /* background-image: linear-gradient(#AE5339); */
  text-decoration: none;
  color: #fff;
  font-size: 14px;
  font-family: sans-serif;
  font-weight: 100;
  height: 5vmin;
  width: 12vmin;
  margin-right:1vw;
  margin-bottom:1vh;
  margin-top:1vh;
  outline: none;
  border: none;
  border-radius: 10px;
  /* border: 1px solid black; */
  cursor: pointer;
}
.glassbutton:active {
  border: 1px solid black;
}